import React from 'react';
import Button from '../components/ui/button';
import BackgroundVideo from './BackgroundVideo';

const HeroSection = () => {
  return (
    <div className="relative min-h-[600px] overflow-hidden">
      {/* Video de fondo */}
      <BackgroundVideo
        src="/videos/hero-background.mp4"
        lowQualitySrc="/videos/hero-background-low.mp4"
        poster="/images/hero-poster.jpg"
      />
      
      {/* Overlay oscuro */}
      <div className="absolute inset-0 bg-black/60"></div>
      
      {/* Contenido */}
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-24 min-h-[600px] flex items-center">
        <div className="text-center max-w-3xl mx-auto text-white">
          <h1 className="text-4xl sm:text-5xl font-bold tracking-tight mb-8">
            Trámites Notariales Simplificados
          </h1>
          
          <p className="text-xl text-gray-200 mb-12">
            Ahorra tiempo y dinero con nuestra calculadora de costos en tiempo real y asesoría profesional personalizada
          </p>
          
          <div className="flex flex-col sm:flex-row justify-center gap-4">
            <Button className="bg-blue-600 hover:bg-blue-700 text-lg px-8 py-4 text-white">
              Calcular Costos Notariales
            </Button>
            <Button className="bg-white text-gray-900 hover:bg-gray-100 text-lg px-8 py-4">
              Agendar Consulta Gratuita
            </Button>
          </div>

          <div className="mt-16 grid grid-cols-1 sm:grid-cols-3 gap-8 text-center">
            <div>
              <div className="text-3xl font-bold text-yellow-500">10+</div>
              <div className="mt-2 text-gray-300">Años de Experiencia Legal</div>
            </div>
            <div>
              <div className="text-3xl font-bold text-yellow-500">30%</div>
              <div className="mt-2 text-gray-300">Ahorro Promedio</div>
            </div>
            <div>
              <div className="text-3xl font-bold text-yellow-500">24/7</div>
              <div className="mt-2 text-gray-300">Soporte Online</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HeroSection;
