import React, { useEffect, useRef, useState } from 'react';
import AdaptiveVideo from './AdaptiveVideo';

const BackgroundVideo = ({ src, lowQualitySrc, poster }) => {
  const [isLoaded, setIsLoaded] = useState(false);
  const videoRef = useRef(null);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.play().catch(() => {
        // Autoplay fue bloqueado, no hacemos nada
      });
    }
  }, []);

  return (
    <div className="absolute inset-0 w-full h-full overflow-hidden">
      <div className="absolute inset-0 bg-black/20"></div>
      <AdaptiveVideo
        ref={videoRef}
        src={src}
        lowQualitySrc={lowQualitySrc}
        poster={poster}
        onLoadedData={() => setIsLoaded(true)}
        className={`absolute top-1/2 left-1/2 min-w-full min-h-full w-auto h-auto transform -translate-x-1/2 -translate-y-1/2 object-cover transition-opacity duration-700 ${
          isLoaded ? 'opacity-100' : 'opacity-0'
        }`}
        autoPlay
        loop
        muted
        playsInline
      />
    </div>
  );
};

export default BackgroundVideo;
