import React, { forwardRef } from 'react';

const AdaptiveVideo = forwardRef(
  ({ src, lowQualitySrc, poster, onLoadedData, className, ...props }, ref) => {
    const handleLoadedData = (event) => {
      if (onLoadedData) {
        onLoadedData(event);
      }
    };

    return (
      <video
        ref={ref}
        src={src}
        poster={poster}
        onLoadedData={handleLoadedData}
        className={className}
        {...props}
      >
        {lowQualitySrc && <source src={lowQualitySrc} type="video/mp4" />}
        <source src={src} type="video/mp4" />
      </video>
    );
  }
);

AdaptiveVideo.displayName = 'AdaptiveVideo';

export default AdaptiveVideo;
